function referenceCarte(item) {
    const element = item["TASK REFERENCE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

function typeCarte(item) {
    const element = item["TASK TYPE"];
    if(element === undefined) return "";
    if(element.includes("MPD")) return "MPD";
    if(element.includes("AIZ")) return "AIZ";
}

function specialite(item) {
    const element = item["SPECIALITE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

function controle(item) {
    const element = item["CONTROLE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

//  ???
function report(item) {
    const element = item["TASK REFERENCE"];
    if(element === undefined) return "";
    return "";
}

function outOfPhase(item) {
    // if(codeCheck === "to be filled") {
    //     return codeCheck;
    // } else if(codeCheck.trim().length === 0) {
    //     return "Y";
    // } else {
    //     return "N"
    // }
    const element = item["OUT OF PHASE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

function categorieMRB(item) {
    const element = item["SOURCE"];
    if(element === undefined) return "";

    if(element.includes("ONESTAR")) {
        return "*";
    }
    if(element.includes("TWOSTAR")) {
        return "**";
    }
    if(element.includes("AIZ")) {
        return "AIZ";
    }
    if(element.includes("MRB,")) {
        return element.replaceAll(/\r|\n/g, "").trim().split("MRB,").filter(x => x.length !== 0)[0].trim().toUpperCase();
    }
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

function codeTache(item) {
    const element = item["TYPE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

function note(item) {
    const element = item["PERIODICITY NOTE"];
    if(element === undefined) return "";
    if(element.replaceAll(/\r|\n/g, "").trim().length !== 0) return "Y";
    return "N";
}

function noteText(item) {
    const element = item["PERIODICITY NOTE"];
    if(element === undefined) return ["", "", ""];
    if(element.replaceAll(/\r|\n/g, "").trim().length !== 0) {

        const cleanText = element.replaceAll(/\r/g, "").replace("NOTE: ", "").replaceAll(/\n/g, " ").trim().toUpperCase();
        if(cleanText.length <= 75) {
            return [
                cleanText, "", ""
            ]
        }

        const splitedText = cleanText.split(" ");
        const result = [];
        let text = "";
        splitedText.forEach(x => {
            if((text.length + x.length + (text.length === 0 ? 0 : 1)) > 75) {
                result.push(text);
                text = x;
            } else {
                text = text + (text.length === 0 ? "" : " ") + x;
            }
        });
        result.push(text);

        while(result.length < 3) result.push("");

        return result;
    }
    return ["", "", ""];
}

function thresholdInterval(item) {
    const calendarCodes = [
        "YE", "MO", "DY", "HR"
    ];

    const result = {
        code1: "",
        value1: "",
        code2: "",
        value2: "",
        code3: "",
        value3: "",
        heures: "",
        cycles: "",
        codeCal: "",
        cal: "",
    };

    const value =  item["PERIODICITY"];

    if(value) {
        const rearrangedValue = value.toUpperCase().replaceAll(/\r/g, "")
            .replaceAll(/FH|AH/g, " FH\n")
            .replaceAll(/FC|AC/g, " FC\n")
            // .replaceAll(/AC/g, "")
            .replaceAll(/YE/g, " YE\n")
            .replaceAll(/MO/g, " MO\n")
            .replaceAll(/DY/g, " DY\n")
            .replaceAll(/HR/g, " HR\n");

        if(value.includes("T:") || value.includes("T :")) {
            const values = rearrangedValue.split("\n");
            
            const filterFH = values.filter(x => (x.includes("T:") || x.includes("T :")) && x.includes("FH"))[0];
            if(filterFH && filterFH.trim().length > 0) {
                result.code1 = "FH"; //filter.split(":")[1].trim().split(" ")[1].trim();
                result.value1 = filterFH.split(":")[1].trim().split(" ")[0].trim();
            }
            const filterFC = values.filter(x => (x.includes("T:") || x.includes("T :")) && x.includes("FC"))[0];
            if(filterFC && filterFC.trim().length > 0) {
                result.code2 = "FC"; //filter.split(":")[1].trim().split(" ")[1].trim();
                result.value2 = filterFC.split(":")[1].trim().split(" ")[0].trim();
            }
            const filterCal = values.filter(x => (x.includes("T:") || x.includes("T :")) &&
            (calendarCodes.map(c => x.includes(c)).filter(f => f == true).length > 0))[0];
            if(filterCal && filterCal.trim().length > 0) {
                result.code3 = filterCal.split(":")[1].trim().split(" ").filter(x => x.trim().length !== 0)[1].trim();
                result.value3 = filterCal.split(":")[1].trim().split(" ")[0].trim();
            }
        }

        if(value.includes("I:") || value.includes("I :")) {
            const values = rearrangedValue.split("\n");
            
            const filterFH = values.filter(x => (x.includes("I:") || x.includes("I :")) && x.includes("FH"))[0];
            if(filterFH && filterFH.trim().length > 0) {
                result.heures = filterFH.split(":")[1].trim().split(" ")[0].trim();
            }
            const filterFC = values.filter(x => (x.includes("I:") || x.includes("I :")) && x.includes("FC"))[0];
            if(filterFC && filterFC.trim().length > 0) {
                result.cycles = filterFC.split(":")[1].trim().split(" ")[0].trim();
            }
            const filterCal = values.filter(x => (x.includes("I:") || x.includes("I :")) &&
            (calendarCodes.map(c => x.includes(c)).filter(f => f == true).length > 0))[0];
            if(filterCal && filterCal.trim().length > 0) {
                result.codeCal = filterCal.split(":")[1].trim().split(" ").filter(x => x.trim().length !== 0)[1].trim();
                result.cal = filterCal.split(":")[1].trim().split(" ")[0].trim();
            }
        }
    }
    return result;
}

function codeCheck(item) {
    const element = item["CODE CHECK"] ? item["CODE CHECK"] : "";
    const elementInfo = item["INFO"] ? item["INFO"] : "";
    if(element === "" && elementInfo === "") return "";
    if(elementInfo.includes("TCC")) {
        return "TCC";
    }
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

function tolerance(item) {
    const element = item["TASK REFERENCE"];
    if(element === undefined) return "";
    return "N";
}

function acZone(item) {
    const element = item["ZONE"];
    if(element === undefined) {
        const result = [];
        while(result.length < 22) result.push("");
        return result;
    };
    const elements = element.replaceAll(/\r/g, "").replaceAll(/,|\s/g, "\n").split("\n").map(x => x.trim().toUpperCase()).filter(x => x.length !== 0);
    while(elements.length < 22) elements.push("");
    return elements;
}

function acces(item) {
    const element = item["ACCESSES"];
    if(element === undefined) {
        const result = [];
        while(result.length < 50) result.push("");
        return result;
    };
    const elements = element.replaceAll(/\r/g, "").replaceAll(/,|\s/g, "\n").split("\n").map(x => x.trim().toUpperCase()).filter(x => x.length !== 0);
    while(elements.length < 50) elements.push("");
    return elements;
}

function ressource(item) {
    const element = item["SPECIALITE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase();
}

function mh(item) {
    const element = item["MH"];
    if(element === undefined) return "";
    if(String(element).toLowerCase().includes("n/a")) return "";
    return String(element).trim().toUpperCase();
}

function number(item) {
    const element = item["TOT NBR PERS"];
    if(element === undefined) return "";
    if(String(element).toLowerCase().includes("n/a")) return "";
    return String(element).trim().toUpperCase();
}

function libelleCarte(item) {
    let element = item["TITLE"];
    let isCut = false;
    if(element === undefined) return ["", isCut];
    element = element.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim().toUpperCase();
    while(element.length > 40) {
        isCut = true;
        const splitedElement = element.split(" ");
        element = splitedElement.slice(0, splitedElement.length - 1).join(" ");
    }
    return [element, isCut];
}

function arrayTextCreation(text) {
    const result = text.split(" ").reduce((r, x) => {
        if((r[r.length - 1] + " " + x).trim().length < 76) {
            return [...r.slice(0, r.length - 1), (r[r.length - 1] + " " + x).trim()];
        } else {
            return [...r, x];
        }
    }, [""]);
    return result;
}

function textSplited75(arr) {
    const result = [];
    arr.forEach(x => {
        if(x.trim().length <= 75) result.push(x.trim());
        else {
            const words = x.trim().split(" ").filter(y => y.length !== 0);
            let element = "";
            words.forEach((w, i) => {
                if((element.length === 0 ? 0 : element.length + 1) + w.length <= 75) {
                    element += (element.length === 0 ? "" : " ") + w;
                } else {
                    result.push(element);
                    element = w;
                }
                if(element.length !== 0 && (i+1) === words.length) {
                    result.push(element);
                }
            })
        }
    })
    return result;
}

function texteLigne(item) {
    let elementDescription = item["DESCRIPTION"];
    let elementPreparation = item["MAINT REQ NOTE"];
    let elementEffectivity = item["PERIODICITY EFFECTIVITY"];

    if(elementDescription === undefined) elementDescription = "";
    if(elementPreparation === undefined) elementPreparation = "";
    if(elementEffectivity === undefined) elementEffectivity = "";

    elementDescription = elementDescription.replaceAll(/\r/g, "").trim().toUpperCase();
    elementPreparation = elementPreparation.replaceAll(/\r/g, "").replaceAll(/NOTE:/g, "").trim().toUpperCase();
    elementEffectivity = elementEffectivity.replaceAll(/\r/g, "").trim().toUpperCase();

    let splitedDescription = textSplited75(elementDescription.split("\n").filter(x => x.length != 0));
    let splitedPreparation = textSplited75(elementPreparation.split("\n").filter(x => x.length != 0));
    let splitedEffectivity = textSplited75(elementEffectivity.split("\n").filter(x => x.length != 0));

    if((splitedDescription.length + splitedPreparation.length + splitedEffectivity.length) <= 14) {
        let result =  [
            ...splitedDescription,
            splitedPreparation != [""] && splitedPreparation.length !== 0 ? "PREPARATION:" : "",
            ...splitedPreparation,
            splitedEffectivity != [""] && splitedEffectivity.length !== 0 ? "EFFECTIVITY:" : "",
            ...splitedEffectivity,
        ].filter(x => x.length !== 0);

        while(result.length < 16) {
            result.push("");
        }

        return result;
    }

    elementDescription = elementDescription.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim();
    elementPreparation = elementPreparation.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim();
    elementEffectivity = elementEffectivity.replaceAll(/\r/g, "").replaceAll(/\n/g, " ").trim();

    splitedDescription = arrayTextCreation(elementDescription);
    splitedPreparation = arrayTextCreation(elementPreparation);
    splitedEffectivity = arrayTextCreation(elementEffectivity);

    if((splitedDescription.length + splitedPreparation.length + splitedEffectivity.length) <= 14) {
        let result =  [
            ...splitedDescription,
            splitedPreparation != [""] ? "PREPARATION:" : "",
            ...splitedPreparation,
            splitedEffectivity != [""] ? "EFFECTIVITY:" : "",
            ...splitedEffectivity,
        ].filter(x => x.length !== 0);

        while(result.length < 16) {
            result.push("");
        }

        return result;
    }

    splitedPreparation = arrayTextCreation("PREPARATION: " + elementPreparation);
    splitedEffectivity = arrayTextCreation("EFFECTIVITY: " + elementEffectivity);

    if((splitedDescription.length + splitedPreparation.length + splitedEffectivity.length) <= 16) {
        let result =  [
            ...splitedDescription,
            ...splitedPreparation,
            ...splitedEffectivity,
        ];

        while(result.length < 16) {
            result.push("");
        }

        return result;
    }

    const wholeText =  elementDescription + (elementPreparation.length !== 0 ? " PREPARATION: " : "") + elementPreparation
        + (elementEffectivity.length !== 0 ? " EFFECTIVITY: " : "") + elementEffectivity;
    const splitedText = arrayTextCreation(wholeText);

    if(splitedText.length === 16) {
        return splitedText;
    }

    // if(splitedText.length > 16) {
    //     return splitedText.slice(0, 16);
    // }
    
    while(splitedText.length < 16) {
        splitedText.push("");
    }

    return splitedText;
}

function ata(item) {
    const element = item["TASK REFERENCE"];
    if(element === undefined) return "";
    return element.replaceAll(/\r|\n/g, "").trim().toUpperCase().slice(0,2);
}

// ???
function referenceDocumentLie(item) {
    const reference = item["NDT/AMM REF"];
    
    if(reference === undefined) return [
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
        {
            reference: "",
            type: "",
        },
    ];

    const result = [];
    if(reference.trim().match(/-A$/)) {
        result.push({
            reference: reference.trim(),
            type: "AMM",
        });
    }
    if(reference.trim().match(/-B$/)) {
        result.push({
            reference: reference.trim(),
            type: "NDT",
        });
    }
    if(reference.includes("ETD")) {
        result.push({
            reference: reference.replace("ETD", "").trim(),
            type: "ETD",
        });
    }
    if(reference.includes("GEK")) {
        result.push({
            reference: reference.replace("GEK", "").trim(),
            type: "GEK",
        });
    }
    if(reference.includes("LMM")) {
        result.push({
            reference: reference.replace("LMM", "").trim().slice(0, 50),
            type: "LMM",
        });
    }
    if(reference.includes("REPAIR")) {
        result.push({
            reference: reference.trim(),
            type: "AMM",
        });
    }
    if(reference.includes("78-30-00 SPECIAL PROCEDURE 008")) {
        result.push({
            reference: reference.trim(),
            type: "AMM",
        });
    }
    
    while(result.length < 5) {
        result.push({
            reference: "",
            type: "",
        });
    }

    return result;
}

