function typeModifiation(item) {
    const element = item["REV"];
    if(element === undefined) return "";
    return "";
}

function comments(acZone, acces, libelleCarte, textLigne, refDocLie) {
    let result = "";
    if(acZone.length > 22) {
        result += "(more than 22 zones)";
    }
    if(acces.length > 50) {
        result += "(more than 50 acces)";
    }
    if(libelleCarte[1]) {
        result += "(title with more than 40 characters)";
    }
    if(textLigne.length > 16) {
        result += "(more than 16 cells for text)";
    }
    if(refDocLie.length > 5) {
        result += "(more than 5 documents linked)";
    }
    return result;
}

function isLowUtilization(item) {
    const value = item["PERIODICITY EFFECTIVITY"];
    if(value) {
        return value.toUpperCase().includes("LOW UTILIZATION");
    }
    return false;
}

function getSameTaskComparationValues(item, previous) {
    const element = item["TASK REFERENCE"];
    if(element) {
        const value = item["PERIODICITY EFFECTIVITY"];
        if(value) {
            const task = element.replaceAll(/\r|\n/g, "").trim();
            let priority;
            if(value.replaceAll(/\r|\n/g, "").trim().toUpperCase() == "ALL") {
                priority = 0;
            } else if(value.toUpperCase().includes("195")) {
                priority = 1;
            } else if(value.toUpperCase().includes("ALL")) {
                priority = 2;
            } else {
                priority = 3;
            }

            if(previous.task == task && previous.priority <= priority) {
                return [previous, false, false];
            }

            if(previous.task == task) {
                return [{
                    task: task,
                    priority: priority
                }, false, true];
            }
            
            return [{
                task: task,
                priority: priority
            }, true, false];
        }
        return [previous, false, false];
    }
    return [previous, false, false];
}


function transformation(arr, staticValuesDict) {
    const transformedArr = [];

    // arr.forEach(x => {
    //     console.log(thresholdInterval(x))
    // })

    let dublonChecker = {
        task: "",
        priority: 9
    }

    arr.forEach(x => {
        const lowUtilization = isLowUtilization(x);

        if(!lowUtilization) {
            let newTaskOrMorePriority;
            let deletePreviousRow;
            [dublonChecker, newTaskOrMorePriority, deletePreviousRow] = getSameTaskComparationValues(x, dublonChecker);

            if(deletePreviousRow) {
                transformedArr.pop();
            }
            console.log(dublonChecker);
            if(newTaskOrMorePriority) {
                const thresIntValues = thresholdInterval(x);
                const cCheck = codeCheck(x);
                const zone = acZone(x);
                const acc = acces(x);
                const libCarte = libelleCarte(x);
                const txtLigne = texteLigne(x);
                const refDocLie = referenceDocumentLie(x);
                const result = {
                    "Rfrence de la Carte": referenceCarte(x),
                    "Type de la Carte": typeCarte(x),
                    "Spcialit": specialite(x),
                    "Contrle": controle(x),
                    "Report": "",
                    "Out of Phase": outOfPhase(x),
                    "Phase" : "",
                    "Poste" : "",
                    "Catgorie MRB": categorieMRB(x),
                    "Code Tache" : codeTache(x),
                    "Note" : note(x),
                    ...noteText(x).reduce((obj, e, i) => {
                        if(Object.keys(obj).length < 3) obj[`Text de la note ${i+1}`] = e;
                        return obj;
                    }, {}),
                    "Code Seuil 1": thresIntValues.code1,
                    "Seuil 1": thresIntValues.value1,
                    "Code Seuil 2": thresIntValues.code2,
                    "Seuil 2": thresIntValues.value2,
                    "Code Seuil 3": thresIntValues.code3,
                    "Seuil 3": thresIntValues.value3,
                    "Heures": thresIntValues.heures,
                    "Cycles": thresIntValues.cycles,
                    "Code Cal.": thresIntValues.codeCal,
                    "Calendaire": thresIntValues.cal,
                    "Code Check": cCheck,
                    "Code Atypique 1": "",
                    "Intervalle Atypique 1": "",
                    "Code Atypique 2": "",
                    "Intervalle Atypique 2": "",
                    "Code Atypique 3": "",
                    "Intervalle Atypique 3": "",
                    "Tolrance - Tolrance": tolerance(x),
                    "Tolrance - Name": "",
                    "Tolrance - Heures": "",
                    "Tolrance - Cycles": "",
                    "Tolrance - Calendaire": "",
                    "Tolrance - Code Calendaire": "",
                    "Tolrance - CPT1": "",
                    "Tolrance - CPT2": "",
                    "Tolrance - CPT3": "",
                    "Mthode de Calcul calendaire": "",
                    "Mthode de Calcul autres": "",
                    ...zone.reduce((obj, e, i) => {
                        if(Object.keys(obj).length < 22) obj[`A/C ZONE ${i+1}`] = e;
                        return obj;
                    }, {}),
                    ...acc.reduce((obj, e, i) => {
                        if(Object.keys(obj).length < 50) obj[`ACCES ${i+1}`] = e;
                        return obj;
                    }, {}),
                    "Famille MSN 1": "",
                    "Famille MSN 2": "",
                    "Famille MSN 3": "",
                    "Famille MSN 4": "",
                    "Famille MSN 5": "",
                    "Ressource (Qualification) 1": ressource(x),
                    "Charge H.h 1": mh(x),
                    "Nombre 1": number(x),
                    "Ressource (Qualification) 2": "",
                    "Charge H.h 2": "",
                    "Nombre 2": "",
                    "Ressource (Qualification) 3": "",
                    "Charge H.h 3": "",
                    "Nombre 3": "",
                    "Libell de la Carte": libCarte[0],
                    ...txtLigne.reduce((obj, e, i) => {
                        if(Object.keys(obj).length < 16) obj[`Text Ligne ${i+1}`] = e;
                        return obj;
                    }, {}),
                    "ATA": ata(x),
                    "Libell de l'ATA (optionnel)": "",
                    "Rfrence document li 1": refDocLie[0].reference,
                    "Type de Document 1": refDocLie[0].type,
                    "Rfrence document li 2": refDocLie[1].reference,
                    "Type de Document 2": refDocLie[1].type,
                    "Rfrence document li 3": refDocLie[2].reference,
                    "Type de Document 3": refDocLie[2].type,
                    "Rfrence document li 4": refDocLie[3].reference,
                    "Type de Document 4": refDocLie[3].type,
                    "Rfrence document li 5": refDocLie[4].reference,
                    "Type de Document 5": refDocLie[4].type,
                    "Numro Amendement": staticValuesDict["mpEdition"],
                    "Date Amendement": staticValuesDict["mpEditionDate"],
                    "Numro Rvision": staticValuesDict["mpdRevisionValue"],
                    "Date Rvision": staticValuesDict["mpdRevisionDate"],
                    "Type de modification": "",
                    "Comments": comments(zone, acc, libCarte, txtLigne, refDocLie),
                };
        
                transformedArr.push(result);
            }
        }
    });

    return transformedArr;
}

