          MP edition:
        </label>
        <input id="mp-edition" type="text" v-model="mpEdition">
        <span class="icon icon-16 icon-info" data-toggle="tooltip" data-placement="right" title="'Numro Amendement' value for AMASIS excel column"></span>
      </div>
      <div class="input-element">
        <label for="mp-edition-date">
          MP edition date:
        </label>
        <input id="mp-edition-date" type="text" v-model="mpEditionDate">
        <span class="icon icon-16 icon-info" data-toggle="tooltip" data-placement="right" title="'Date Amendement' value for AMASIS excel column"></span>
      </div>
      <div class="input-element">
        <label for="mpd-revision-value">
          MPD revision value:
        </label>
        <input id="mpd-revision-value" type="text" v-model="mpdRevisionValue">
