import { transformation } from "@/js-modules/functionsArkia2";
const XLSX = require("xlsx");

export default {
  name: 'MPDTransformationATR',
  data() {
    return {
      workbook: "",
      download_workbook: "",

      mpEdition: "R26",
      mpEditionDate: "30/09/2023",
      mpdRevisionValue: "R30",
      mpdRevisionDate: "14/07/2023",
    }
  },
  computed: {
    allInfoReady() {
      return this.workbook === '';
    },
    allVariablesAffectingDownload() {
      return [
        this.workbook,
        this.mpEdition,
        this.mpEditionDate,
        this.mpdRevisionValue,
        this.mpdRevisionDate
      ]
    }
  },
  methods: {
    readFile() {
      const input = document.querySelector("#file-input-arkia");
      this.file = input.files[0];
      const reader = new FileReader();

      reader.onload = (e) => {
        /* Parse data */
        const bstr = e.target.result;
        const wb = XLSX.read(bstr, { type: 'binary' });
        // console.log(wb);
        this.workbook = wb;
      }

      // console.log(reader.readAsBinaryString(this.file));
      return reader.readAsBinaryString(this.file);
    },
    transformFile() {
      this.transformFileNormal()
    },
